#my-calendar .mode {
  display: flex;
  justify-content: flex-start;
  padding: 10px;
}
#my-calendar .mode input {
  width: 18px;
  margin-right: 20px;
}
#my-calendar thead {
  cursor: default;
}
#my-calendar thead td {
  min-width: 30px;
}
#my-calendar thead td .button {
  cursor: pointer;
}
#my-calendar thead td[colspan] {
  text-align: center;
  font-weight: bold;
}
#my-calendar thead tr:last-child td {
  text-align: center;
}
#my-calendar tbody td,
#my-calendar tfoot td {
  padding: 0;
  text-align: center;
  font-size: 14px;
}
#my-calendar tbody td.free,
#my-calendar tbody td.is-payed,
#my-calendar tbody td.un-payed {
  cursor: pointer;
}
#my-calendar tfoot td:first-child {
  text-align: right;
}
#my-calendar td {
  vertical-align: middle;
  text-align: center;
}
#my-calendar td.final {
  display: inline-flex;
  flex-direction: row;
}
#my-calendar td.final .summary-cell {
  display: flex;
}
#my-calendar .final .total,
#my-calendar .final .is-payed,
#my-calendar .final .un-payed {
  cursor: pointer;
  min-width: 40px;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#my-calendar .total {
  cursor: pointer;
  min-width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#my-calendar .is-payed {
  min-width: 40px;
  color: white;
  background: green;
}
#my-calendar .un-payed {
  min-width: 40px;
  background: darkred;
  color: white;
}
.popup {
  top: -1000px;
  left: -1000px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 280px;
  max-height: 180px;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 0 15px #000000;
}
.popup tbody td {
  text-align: left;
}
.popup.hidden {
  display: none;
}
.popup p {
  margin: 0;
  padding: 0;
}
